<script>
import Vue from 'vue';
import { Confirm, Alert, Toast, Notify, Loading } from 'vue-ydui/dist/lib.rem/dialog';
/* pximport { Confirm, Alert, Toast, Notify, Loading } from 'vue-ydui/dist/lib.px/dialog'; */
Vue.prototype.$dialog = {
    confirm: Confirm,
    alert: Alert,
    toast: Toast,
    notify: Notify,
    loading: Loading,
};
export default {
    components:{
        /* */
        turntable:require('./turntable2.vue'),
    },
    data () {
        return{
            showBox: false, //(false )
            box: "", //(1:,2:,3:,4:,5:,6,7.,8:)
            LoginInfo:false,
            luckInfo:"",
            activityInfo:[]
        }
    },
    watch:{
        showBox(val){
            if(val == true){
                $('html,body').css({"overflow-y":"hidden","height":"100%"})
            }else{
                $('html,body').removeAttr("style")
            }
        }
    },
    mounted () {
        this.$nextTick(function () {
            /*1:,
            2:,
            3:,
            4:,
            5:,
            6:*/
            this.info()
        })
    },
    methods: {
        /**/
        info () {
            let _that = this
            let Base64 = require('js-base64').Base64
            let Url = '/mapi2/index.php?'
            let formData = new FormData();
            formData.append("act","uc_center")
            _that.$http.post(Url,formData)
                .then(response => {
                    var response = JSON.parse(Base64.decode(response.data));
                    if(response.response_code == 0){
                        _that.LoginInfo = false
                    }else{
                        _that.LoginInfo = true
                    }
                })
        },
        /* */
        pointerInfo(data){
            if(data.show_err_code == undefined){
                this.box = 1
            }
            if(data.show_err_code == 1){
                this.showBox = true
                this.box = 6
            }
            if(data.show_err_code == 2){
                this.showBox = true
                this.box = 3
            }
            if(data.show_err_code == 3){
                this.showBox = true
                this.box = 2
            }
            if(data.show_err_code == 4){
                this.showBox = true
                alert('')
            }
            if(data.show_err_code == 5){
                this.showBox = true
                this.box = 5
            }
            if(data.show_err_code == 6){
                this.showBox = true
                this.box = 4
            }
        },
        /* namevalue*/
        callbackInfo(data){
            this.luckInfo = data.name
            this.showBox = true
        },
        /**/
        activityRule(data){
            if(data.response_code == 0){
                /*0:()*/
                this.showBox = true
                this.box = 4
            }else if(data.response_code ==2){
                /*2:*/
                this.showBox = true
                this.box = 7
            }else if(data.response_code ==3){
                /*3:*/
                this.showBox = true
                this.box = 8
            }else if(data.response_code ==4){
                /*4:*/
                this.showBox = true
                this.box = 6
            }
            if(data.activity != null){
                this.activityInfo = data.activity
            }
        },
        closeBox () {
            this.showBox = false;
        },
        applogin(){ //
            zichanjia_app_android.applogin();
        },
        sharedraw(){
            zichanjia_app_android.sharedraw();
        },
        zichanjiaActivityType(){ //
            var obj = {type: "0", cate_id: "123",borrow_id: "234"}
            zichanjia_app_android.activity(JSON.stringify(obj));
        },
        openType(){
            var browser = {
                versions: function() {
                    var u = navigator.userAgent,
                        app = navigator.appVersion;
                    return { //
                        trident: u.indexOf('Trident') > -1, //IE
                        presto: u.indexOf('Presto') > -1, //opera
                        webKit: u.indexOf('AppleWebKit') > -1, //
                        gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //
                        mobile: !!u.match(/AppleWebKit.*Mobile.*/), //
                        ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios
                        android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //androiduc
                        iPhone: u.indexOf('iPhone') > -1, //iPhoneQQHD
                        iPad: u.indexOf('iPad') > -1, //iPad
                        webApp: u.indexOf('Safari') == -1 //web
                    };
                }(),
                language: (navigator.browserLanguage || navigator.language).toLowerCase()
            }

            if (browser.versions.mobile) { //browser
                var ua = navigator.userAgent.toLowerCase(); //
                if (browser.versions.ios) {
                    alert("ios")
                }
                if (browser.versions.android) {
                    alert("android")
                }
                if (ua.match(/MicroMessenger/i) == "micromessenger") {
                    alert("MicroMessenger")
                }
                if (ua.match(/\sQQ/i) == "qq") {
                    alert("qq")
                }
            } else {
                //PC
                alert("PC")
            }
        },
        userName(){ return window.storeWithExpiration.get('MallUserName')},
        userPwd(){return window.storeWithExpiration.get('MallUserPwd')},
    },
    filters:{
        bannerimgurl (url){
            let _self = this
            if(url != null && url != ""){

                // : http://localhost:6981/InspectionData/TEST.html
                var fullPath=window.document.location.href;

                // : /InspectionData/TEST.html
                var pathName=window.document.location.pathname;

                // : /InspectionData/TEST.html25
                var pos=fullPath.indexOf(pathName);

                // : http://localhost:6981
                var localhostPath=fullPath.substring(0,pos);

                if(url.indexOf("./") > -1){
                    var l = url.substr(1, url.length);
                    if(localhostPath != 'http://localhost:8080'){
                        return localhostPath + l;
                    }else{
                        return 'http://develop.jianguolicai.cn/' + l;
                    }

                }else{
                    return url;
                }
            }else {
                return require('../../../assets/images/goods_bg.gif')
            }
        },
        newDateTime(timeStamp){
            var date = new Date();
            date.setTime(timeStamp * 1000);
            var y = date.getFullYear();
            var m = date.getMonth() + 1;
            m = m < 10 ? ('0' + m) : m;
            var d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            var h = date.getHours();
            h = h < 10 ? ('0' + h) : h;
            var minute = date.getMinutes();
            var second = date.getSeconds();
            minute = minute < 10 ? ('0' + minute) : minute;
            second = second < 10 ? ('0' + second) : second;
            return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
        }
    }
}

</script>
